.page-header {
  min-height: 56px;
  // margin-bottom: -36px;
  position: relative;
  z-index: 1;
  padding-bottom: 56px;
}

.page-header__logo {
  position: absolute;
  transform: translate(-50%);
}

.page-header__logo-image {
  max-width: 100%;
}

@media (min-width: @tablet-width) {
  .page-header {
    margin-bottom: 0;
    padding-bottom: 0;
  }

  .page-header__logo {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    z-index: 1;
  }
}

@media (min-width: @desktop-width) {
  .page-header__logo-image {
    width: 187px;
    height: 97px;
  }
}
