.navigation {
  margin: 0;
  padding: 0;
  color: #ffffff;
}
.navigation--nojs .navigation__list {
  display: flex;
  flex-direction: column;
  visibility: visible;
  opacity: 1;
  height: 224px;
}
.navigation--nojs .navigation__toggle {
  display: none;
}
.navigation__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  width: 23px;
  height: 22px;
  padding: 0;
  background: 0 0;
  border: none;
  user-select: none;
}
.navigation__toggle > .navigation__toggle-icon {
  position: relative;
  display: inline-block;
  width: 23px;
  height: 4px;
  vertical-align: bottom;
  background: #81b3d2;
  transition: background 0.3s 0.5s;
}
.navigation__toggle > .navigation__toggle-icon:hover,
.navigation__toggle > .navigation__toggle-icon:focus {
  opacity: 0.6;
}
.navigation__toggle > .navigation__toggle-icon:active {
  opacity: 0.3;
}
.navigation--opened .navigation__toggle > .navigation__toggle-icon {
  background: 0 0;
}
.navigation__toggle > .navigation__toggle-icon::after,
.navigation__toggle > .navigation__toggle-icon::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background: #81b3d2;
  transform-origin: 50% 50%;
  transition: top 0.3s 0.3s, transform 0.3s, background 0.5s;
}
.navigation__toggle > .navigation__toggle-icon::before {
  top: 9px;
}
.navigation__toggle > .navigation__toggle-icon::after {
  top: -9px;
}
.navigation--opened .navigation__toggle > .navigation__toggle-icon::before {
  transform: rotate3d(0, 0, 1, 45deg);
  background: #ffffff;
}
.navigation--opened .navigation__toggle > .navigation__toggle-icon::after,
.navigation--opened .navigation__toggle > .navigation__toggle-icon::before {
  top: 0;
  transition: top 0.3s, transform 0.3s 0.5s;
}
.navigation--opened .navigation__toggle > .navigation__toggle-icon::after {
  transform: rotate3d(0, 0, 1, -45deg);
  background: #ffffff;
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: height 0.5s, visibility 0.5s, opacity 0.5s linear;
}
.navigation__list--visible {
  display: block;
  visibility: visible;
  opacity: 1;
  height: 224px;
  transition: height 0.5s, visibility 0.5s, opacity 0.5s linear;
}
.navigation__list::after {
  content: "";
  width: 320px;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  margin-top: -7px;
  display: block;
}
.navigation__wrapper {
  width: 100%;
}
.navigation__item {
  padding: 0;
  margin: 0;
  min-height: 56px;
  background-color: #81b3d2;
  font-size: 18px;
  font-weight: 700;
  line-height: 40px;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}
.navigation__item:first-child {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
}
.navigation__item:last-child {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.navigation__item:hover,
.navigation__item:focus {
  background-color: #669ec0;
}
.navigation__item:active {
  background-color: #5496bd;
}
.navigation__item:active .navigation__link {
  color: rgba(255, 255, 255, 0.3);
}
.navigation__link {
  text-decoration: none;
  color: #ffffff;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  padding: 0 40px;
}
@media (min-width: 768px) {
  .navigation--nojs .navigation__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: auto;
  }
  .navigation__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: #ffffff;
    visibility: visible;
    opacity: 1;
    height: auto;
    transition: none;
  }
  .navigation__list--visible {
    transition: none;
  }
  .navigation__list::after {
    content: none;
  }
  .navigation__item {
    background-color: #ffffff;
    box-shadow: none;
    min-height: 56px;
    min-width: 153px;
    font-size: 14px;
    font-weight: 700;
    line-height: 26px;
  }
  .navigation__item:nth-child(3) {
    margin-left: auto;
  }
  .navigation__item:nth-child(odd),
  .navigation__item:nth-child(even) {
    box-shadow: none;
  }
  .navigation__item:hover,
  .navigation__item:focus,
  .navigation__item:active {
    background-color: inherit;
  }
  .navigation__item:active .navigation__link {
    color: #000000;
    opacity: 0.3;
  }
  .navigation__item--current .navigation__link {
    color: #766357;
  }
  .navigation__item--current .navigation__link:hover {
    color: #604e43;
  }
  .navigation__item--current .navigation__link:active {
    color: #766357;
    opacity: 0.3;
  }
  .navigation__link {
    color: #000000;
    display: block;
    padding: 0;
  }
  .navigation__link:hover,
  .navigation__link:focus {
    color: #81b3d2;
  }
  .navigation__link:active {
    opacity: 0.3;
  }
  .navigation__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .navigation__item {
    min-width: 239px;
  }
}
